import React from 'react'
import style from './Sort.module.scss'

type PropsType = {
    setSortUsers: (sortValue: 'name' | 'address' | 'company') => void
    sortValue: 'name' | 'address' | 'company'
}

const Sort: React.FC<PropsType> = ({setSortUsers, sortValue}) => {
    return (
        <div className={style.sortContainer}>
            <div className={style.sort}>
                <p>:</p>
                {sortValue === 'name' ? <button onClick={ () => setSortUsers('address')}> </button> : <button onClick={() => setSortUsers('name')}> </button>}
                <button onClick={() => setSortUsers('company')}> </button>
            </div>
        </div>
    )
}

export default Sort